















import { tags } from "@/api/tags";
export default {
  data() {
    return {
      list: []
    };
  },
  mounted() {
    tags().then(res => {
      this.list = res.data.tags.slice(10);
    });
  },
  methods: {
    handleTabClick(data) {
      this.$emit("tagClick", data);
    }
  }
};
